import { gsap } from "gsap";
import { disableBodyScroll, enableBodyScroll} from 'body-scroll-lock';

let opened = false;
const header = document.querySelector('header');
const menu = header.querySelector('.menu');
const links = document.querySelectorAll('a');
const burger = header.querySelector('.burger');
const animateMenu = gsap.timeline({ paused: true }).to(menu, { duration: .3, autoAlpha: 1 });

export default function superMenu() {

  scrollToAnchor();
  burger.addEventListener('click', toggleMenu);

  function toggleMenu() {

    if (!opened) {
      showMenu();
    } else {
      hideMenu();
    }
  }
}

function showMenu() {
  disableBodyScroll(menu);
  header.classList.add('menu-open');
  animateMenu.play();
  opened = true;
}

function hideMenu() {
  enableBodyScroll(menu);
  header.classList.remove('menu-open');
  animateMenu.reverse();
  opened = false;
}

function scrollToAnchor() {

  document.querySelector('main').scrollIntoView();

  [...links].forEach(link => {
    link.addEventListener('click', (e) => {

      let anchor = checkAnchor(link);

      hideMenu();

      if (anchor) {
        e.preventDefault();
        let section = document.querySelector(anchor);
        section.scrollIntoView({
          behavior: 'smooth'
        });
      }
    })
  });
}

// Detect if a link's href goes to the current page
function checkAnchor(link) {
  if (
    link.protocol !== window.location.protocol ||
    link.host !== window.location.host ||
    link.pathname !== window.location.pathname ||
    link.search !== window.location.search
  ) {
    return false;
  }
  return link.hash;
}