import { gsap } from "gsap";
export default function preloader() {

  const preloader = document.getElementById('preLoader');
  const preloaderContent = preloader.getElementsByClassName('preloader-content')[0];
  const cTop = preloader.getElementsByClassName('curtain-top')[0];
  const cBottom = preloader.getElementsByClassName('curtain-bottom')[0];
  const showSite = gsap.timeline({ paused: true });

  showSite
    .to(cTop, { duration: 0.5, yPercent: 100, ease: "power2.inOut" }, "preload")
    .to(cBottom, { duration: 0.5, yPercent: -100, ease: "power2.inOut" }, "preload")
    .to(preloaderContent, { duration: 0.2, autoAlpha: 0 }, "preload+=0.5")
    .to([cTop, cBottom], { duration: 0.6, yPercent: 0, ease: "power2.inOut" }, "preload+=0.8");

  window.addEventListener('load', () => {
    setTimeout(() => {
      showSite.play();
    }, 2000);
  });
}